
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
 
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
 
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
import Chartist from 'chartist/dist/chartist.min.js';
import ErrorComponent from '../components/ErrorComponent';

var ws_url = new URL("/ws/etlflow" , window.location.href)
ws_url.protocol = ws_url.protocol.replace("http","ws")

// var ws = new WebSocket('wss://devetlflow.startv.com/ws/etlflow/eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyIjoibWludF9hZG1pbiIsICJyb2xlIjoiYWRtaW4ifQ.gwiUv0vss-3gX5rkPOGTdJ_81n5N3W7Hv1yeGsJj5g8')

// new WebSocket('wss://devetlflow.startv.com/ws/etlflow/eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyIjoibWludF9hZG1pbiIsICJyb2xlIjoiYWRtaW4ifQ.gwiUv0vss-3gX5rkPOGTdJ_81n5N3W7Hv1yeGsJj5g8')
// new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
 
class MemoryGraph extends React.Component {
  constructor(props){
    super(props);
    const GRAPHQL_URL = '/ws/etlflow'
 
    this.state  = {
      used: '',
      total: '',
      free: '',
      max: '',
      error:'',
      error_code:'',
    }
    this.ws = new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
 
    this.options =  {
      low: 0,
      showArea: true,
      showPoint: false
    };
  }
 
  call(close_ws){
 
    if(close_ws === "true"){
      this.ws.close(1000,"User closed the connection")
      document.getElementById("statslinknotify").innerText = "Real-Time Stats (Offline)"
      this.setState(prevState => ({
        messages:[]            
        }))
      console.log('ws closed')
    }
    else{
      document.getElementById("statslinknotify").innerText = "Real-Time Stats (Online)"
      this.ws = new WebSocket(ws_url + "/" + localStorage.getItem('Authorization'))
 
      this.ws.onopen = () => {
      console.log('ws connected')
      }  
  
      this.ws.onmessage = evt => {
      const message = evt.data
      const obj = JSON.parse(message);
      var res = obj.memory;
      this.setState(prevState => ({
        used: res.used,
        total: res.total,
        free: res.free,
        max: res.max
      }))
      } 
  
      this.ws.onclose = () => {
        console.log('disconnected')
      }
 
      var used_memory_data = {
       series: [[]]
        };
   
      var free_memory_data = {
        series: [[]]
      };
      
      var total_memory_data = {
        series: [[]]
      };
  
      var used_memory_chart = new Chartist.Line('.ct-chart', used_memory_data, this.options);
      var free_memory_chart = new Chartist.Line('.cpu-chart', free_memory_data, this.options);
      var total_memory_chart = new Chartist.Line('.cpu-chart-1', total_memory_data, this.options);
 
      
      this.updateUsedMemoryChart(used_memory_chart, used_memory_data, this.getUsedMemoryData(), 30)
      this.updateFreeMemoryChart(free_memory_chart, free_memory_data, this.getFreeMemoryData(), 30)
      this.updateTotalMemoryChart(total_memory_chart, total_memory_data, this.getTotalMemoryData(), 30)
    }
  }

  componentDidMount() {
    this.call()
  }
 
  componentWillUnmount() {
    this.ws.close()
  }

  getUsedMemoryData() {
    var seriesVals = [];
    seriesVals.push(this.state.used)
    // console.log(JSON.stringify(seriesVals[0]))
    return seriesVals[0];
  }

  getFreeMemoryData() {
    var seriesVals = [];
    seriesVals.push(this.state.free)
    // console.log(JSON.stringify(seriesVals[1]))
    return seriesVals[0];
  }

  getTotalMemoryData() {
    var seriesVals = [];
    seriesVals.push(this.state.total)    
    return seriesVals[0];
  }

  updateUsedMemoryChart(chart,data,point,length) {
    if(data.series[0].length >= length) {
      data.series[0].shift();
    }
    data.series[0].push(point);
    chart.update(data);
    setTimeout(() => { this.updateUsedMemoryChart(chart,data,this.getUsedMemoryData(),length) }, 1000);
  }
 
  updateFreeMemoryChart(chart,data,point,length) {
    if(data.series[0].length >= length) {
      data.series[0].shift();
    }
    data.series[0].push(point);
    chart.update(data);
    setTimeout(() => { this.updateFreeMemoryChart(chart,data,this.getFreeMemoryData(),length) }, 1000);
  }
 
  updateTotalMemoryChart(chart,data,point,length) {
    if(data.series[0].length >= length) {
      data.series[0].shift();
    }
    data.series[0].push(point);
    chart.update(data);
    setTimeout(() => { this.updateTotalMemoryChart(chart,data,this.getTotalMemoryData(),length) }, 1000);
  }

   render(){ 
    if (this.state.error !== ''){
      return (
        <div>
          <ErrorComponent error={this.state.error}/>
        </div>
      );
     } else {
      return (
      <div class="card">
      <div class="card-header">
        <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify" role = "button" 
          aria-expanded = "true" aria-controls = "streamnotify" id = "statslinknotify">
        </a>
        <ul class = "pagination float-right">
              <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                      this.call("false")
                }} >
                Connect
              </button>
              <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                  this.call("true")
                }} >
                Close
              </button>
        </ul>      
      </div>
    <div className = "collapse" id = "streamnotify">
    <div class= "card-body">
    <div class = "row">
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">Used Memory</div>
              <div class= "card-body">
                  <div class="ct-chart"></div>
              </div>
          </div>
      </div>
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">Free Memory</div>
              <div class= "card-body">
                  <div class="cpu-chart"></div>
              </div>
          </div>
      </div>
      <div class="col-sm-3">
          <div class="card">
              <div class="card-header">Total Memory</div>
              <div class= "card-body">
                  <div class="cpu-chart-1 "></div>
              </div>
          </div>
      </div>
      <div class="col-sm-3">
        <div class="card">
           <div class="card-header">Current Usage</div>
              <div class= "card-body">
                <div>
                   <ul class="list-group ">
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Used Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.used}</span>
                       </li>
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Free Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.free}</span>
                       </li>
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Total Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.total}</span>
                       </li>
                       <li class="list-group-item d-flex justify-content-between align-items-center">
                         <b>Max Memory</b>
                         <span class="badge badge-secondary badge-pill">{this.state.max}</span>
                       </li>
                   </ul>
                </div> 
              </div>
      </div>
      </div>
    </div>
    <br/>
    </div>
    </div>
    </div>
  );}
  }       
}
 
export default MemoryGraph;
 

