
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
 
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
 
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
import ErrorComponent from '../components/ErrorComponent';
import MemoryGraph from '../pages/MemoryGraph';
import Metrics from '../pages/Metrics';
import BarGraph from '../pages/BarGraph';

class Stats extends React.Component {
  constructor(props){
    super(props);
    this.state  = { 
        error:'',
        error_code:'',
      }
  }

  render(){

    if (this.state.error !== ''){
      return (
        <div>
          <ErrorComponent error={this.state.error}/>
        </div>
      );
     } else {
    return (
      <div class = "container-fluid">
                <MemoryGraph GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.logout}  />    
         <br/>
        <div class = "row">
            <div class="col-sm-9">
                <BarGraph GRAPHQL_URL = {this.props.GRAPHQL_URL}  logout={this.props.logout} />   
            </div>
            <div class="col-sm-3">
                <Metrics  GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.logout}  />  
            </div>    
        </div>
      </div>
  );}
  }       
}
 
export default Stats;
 

