import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

import '../css/App.css'

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"

class NotificationComponent extends React.Component {
  constructor(props){
    super(props); 
  }

  render(){  

    var notification_var = ""
    if(this.props.notification){
      if(this.props.is_error) {
         notification_var =
         <div class="alert alert-danger alert-dismissible fade show" role="alert">
            <h4 class="alert-heading">{this.props.message}</h4>
            <p>{this.props.errors}</p>
            <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.props.setNotification(false)}}>
               <span aria-hidden="true">&times;</span>
            </button>
         </div>
       } else {
          notification_var =
          <div class="alert alert-success alert-dismissible fade show" role="alert">
             <h4 class="alert-heading">{this.props.message}</h4>
             <p>{this.props.job_props}</p>
             <button type="button" class="close" data-dismiss="alert" aria-label="Close" onClick = {() => {this.props.setNotification(false)}}>
                <span aria-hidden="true">&times;</span>
             </button>
          </div>
        }  
    }

    return (
        <div>
          {notification_var}
        </div>
    );
  }
}
export default NotificationComponent;
