import React from 'react';

import jwt_decode from "jwt-decode";
import CurrentTime from './CurrentTime';
import Stats from '../cards/Stats';
import Credentials from '../cards/Credential';
import JobList from '../cards/JobList';
import JobRuns from '../cards/JobRuns';
import JobLogs from '../cards/JobLogs';
import ErrorComponent from '../components/ErrorComponent';

import '../css/App.css'
import {
  HashRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import JobRunDetails from '../pages/JobRunDetails';


class Routes extends React.Component {

  constructor(props){
    super(props); 
    this.state  = {
      jobs: [],
      metrics: [],
      user: '',
      loader:false,
      error:'',
      error_code:''
    }
  }

  async fetchEtlFlowJobs() {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true,
      error:''
    }))
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
              jobs {
                name 
                schedule
                props {key value} 
                nextSchedule 
                schduleRemainingTime 
                is_active 
                last_run_time
                last_run_description
             }
           }
          `
        }
      )   
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    if (!response.ok) {
      var message = `An error has occured: ${response.status}`;
      if (response.status === 403) {
        message = `net::ERR_ABORTED 403 (Forbidden). Please Logout ...`;
        this.props.doLogout() 
        localStorage.removeItem("Authorization")
        throw  new Error(message);
      } else { 
        throw  new Error(message);
      }
    } 
    else {  
      const {data} = await response.json();
      this.setState(prevState => ({
      loader: false
     }))

    return data;    
    }
  }

  setInterval() {
    this.timerID = setInterval(
      () => this.get_etl_jobs(),
      1000 * 1000
    );
  }

  fetchEtlFlowUserDetails() {
    const token = localStorage.getItem('Authorization')
    var decoded = JSON.parse(JSON.stringify(jwt_decode(token)));
    this.setState({
      user: decoded["user"]
    })
  }

  componentDidMount() {

    //calling jobs api
    this.get_etl_jobs()
    this.fetchEtlFlowUserDetails()
 }

  get_etl_jobs = () => {
   this.fetchEtlFlowJobs().then(({jobs}) => {
     this.setState(prevState => ({
       jobs: jobs
     }))
   }).catch(error => {
     this.setState(prevState => ({
       error: error,
       loader: false
     }))
    });
  }

  remove_token() {
   localStorage.removeItem("Authorization")
   this.props.doLogout("")
}

  render(){
    return (
       <Router>
          <nav className = "navbar navbar-expand-lg navbar-dark bg-dark mb-3">
              <ul className = "nav navbar-nav w-100">
                <li className = "nav-item nav-link" ><Link to="/">Jobs</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/JobRuns">Job Runs</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/JobLogs">Job Logs</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/Stats">Stats</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/Credentials">Credentials</Link> </li>
              </ul>
              <div class="navbar-collapse collapse w-100 order-3 dual-collapse2">
              <ul class="nav justify-content-end w-100">
              <CurrentTime GRAPHQL_URL = {this.props.GRAPHQL_URL}/>
                <li class="nav-item dropdown">
	                <a class="nav-link  dropdown-toggle" href="#" data-toggle="dropdown">{this.state.user}</a>
	                  <ul class="dropdown-menu">
		                  <li>
                        <a class="dropdown-item" href="https://github.com/tharwaninitin/etlflow">
                           Git Hub 
                        </a>
                      </li>
                      <li>
                        <a class="dropdown-item" href="https://tharwaninitin.github.io/etlflow/site/">
                          Docs
                        </a>
                      </li>
                      <li>
                        <a class="dropdown-item">
                          <input type="button" class="btn btn-secondary btn-sm" value="LogOut" onClick = {() => this.remove_token()}></input>
                        </a>
                      </li>
	                  </ul>
	              </li>
              </ul>
            </div>
          </nav>
          <Switch>
              <Route exact path="/" component={() => (<JobList GRAPHQL_URL = {this.props.GRAPHQL_URL} jobs={this.state.jobs} logout={this.props.doLogout} get_etl_jobs = {this.get_etl_jobs} error = {this.state.error}/>)}/>
              <Route path="/JobRuns/:job_name?" render={props => (<JobRuns GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.doLogout} jobs={this.state.jobs}/>)}/>
              <Route path="/JobLogs" render={props => (<JobLogs GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.doLogout}/>)}/>
              <Route path="/Stats" render={props => (<Stats GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.doLogout}/>)}/>
              <Route path="/Credentials" render={props => ( <Credentials GRAPHQL_URL = {this.props.GRAPHQL_URL} logout={this.props.doLogout} />)}/>
              <Route path="/JobRunDetails/:id" render={props => ( <JobRunDetails GRAPHQL_URL = {this.props.GRAPHQL_URL}/>)}/>
          </Switch>
       </Router>
    );
    }
}
export default Routes;
// export default withRouter(Routes)

