import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
  
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import LoaderComponent from '../components/LoaderComponent';
import ErrorComponent from '../components/ErrorComponent';
import JobLogsData from '../pages/JobLogs';

class JobLogs extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { queueStats: [],
      loader:false,
      error:'',
      error_code:0,
      errors: [],
    }
  }

  async fetchEtlFlowQueueStats() {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true,
      error: ''
    }))

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
            queueStats {
              name 
              details
              submitted_from
              execution_time
              }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    if (!response.ok) {
      var message = `An error has occured: ${response.status}`;
      if (response.status === 403) {
        
        message = `net::ERR_ABORTED 403 (Forbidden). Please Logout ...`;
        this.props.logout() 
        localStorage.removeItem("Authorization")
        throw  new Error(message);
      } else { 
        throw  new Error(message);
      }
    } 
    else {  
      
      this.setState(prevState => ({
        loader: false
      }))

      const {data} = await response.json()
      return data;   
    } 
  }

  
  setJobErrorDeails(is_error,errors){
    this.setState(prevState => ({
      is_error: is_error,
      errors: errors                    
      }))
  }

  componentDidMount() {
    //calling getInfo api
    this.setEtlFlowQueueStats()
  }

  setEtlFlowQueueStats = () => {
    this.fetchEtlFlowQueueStats().then(({queueStats}) => {
      this.setState(prevState  => ({
        queueStats: queueStats
      }))
    }).catch((error) => {
      this.setState(prevState => ({
        error: error,
        loader: false,
      }))
  }); 
  }

  showModal(data)
  {
    var props = ''
    var queue = JSON.parse(data)
 
    for(var key in queue) {
    { 
        props  += "<b>"+ key + "</b>" +"=" + queue[key]+ "<br></br>"
    }

   {$("#queueStats .modal-body").html(props)}
   $("#queueStats").modal();
  }
  }

  render(){
    if (this.state.error !== ''){
      return (
        <div class="container">
         <ul class = "pagination justify-content-center">
             <button class= "btn btn btn-sm" type="button " onClick = {() => {
                    this.setEtlFlowQueueStats()
                }}>
               Refresh
             </button>
         </ul>
          <ErrorComponent error={this.state.error}/>
        </div>
      );
    } 
    else if (this.state.loader){
      return (
        <div>
          <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
        </div>
      );
    }
    else{ 
      return (
        <div>
          <JobLogsData queueStats = {this.state.queueStats} setEtlFlowQueueStats = {this.setEtlFlowQueueStats} />
       </div>
    ); 
    }         
  }
}
export default JobLogs;
