
import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
 
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import ChartistGraph from 'react-chartist';
import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
 
import 'chartist/dist/chartist.min.css';
import 'chartist/dist/chartist.js';
import Chartist from 'chartist/dist/chartist.min.js';
import ErrorComponent from '../components/ErrorComponent';

class BarGraph extends React.Component {
  constructor(props){
    super(props);
    const GRAPHQL_URL = '/ws/etlflow'
 
    this.state  = {
      jobLogs:[],
      filter: '',
      limit: '',
      error:'',
      error_code:'',
    }
 
    this.filterOperation = ['2','5','10']
    this.limitOperation = ['2','5','10','30','50']
 
    this.options = {
      low: 0,
      showArea: true,
      showPoint: false
    };
  }
 
  async callApi() {
    const GRAPHQL_URL = this.props.GRAPHQL_URL
    const token = localStorage.getItem('Authorization')
 
    var query =  ` query { jobLogs { job_name success failed }}`
   
    if(this.state.filter !== '' && this.state.limit !== '') {
      query =  ` query { jobLogs(limit: ${this.state.limit}, filter: ${this.state.filter}  ) { job_name success failed }}`
    } else  if (this.state.filter !== '') {
      query =  ` query { jobLogs(filter: ${this.state.filter}) { job_name success failed }}`
    } else  if (this.state.limit !== '') {
      query =  ` query { jobLogs(limit: ${this.state.limit}) { job_name success failed }}`
    }
 
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      ) 
    })

    this.setState(prevState => ({
      loader: true,
      error: ''
    }))

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 


    if (!response.ok) {
      var message = `An error has occured: ${response.status}`;
      if (response.status === 403) {
        
        message = `net::ERR_ABORTED 403 (Forbidden). Please Logout ...`;
        this.props.logout() 
        localStorage.removeItem("Authorization")
        throw  new Error(message);
      } else { 
        throw  new Error(message);
      }
    } 
    else {  
      const {data} = await response.json()
      return data;   
    }
  }

  setErrorState(error,error_code) {
    this.setState(prevState => ({
      error: error,
      error_code: error_code
    }))
  }

  barChart(){
    new Chartist.Bar('.ct-chart-2', {
      labels: [0],
      series: [
        [0],
        [0],
      ]
    }, {
      seriesBarDistance: 10,
      axisX: {
        offset: 60
      },
      axisY: {
        offset: 80,
        labelInterpolationFnc: function(value) {
          return value
        },
        scaleMinSpace: 15
      }
    });       
  }
 
  barChartData(){
    var job_name_labels = []
    var job_success = []
    var job_failed = []
 
    this.state.jobLogs.map( (data) => {
     job_name_labels.push(data.job_name)
     job_success.push(data.success)
     job_failed.push(data.failed)
    })
   
    new Chartist.Bar('.ct-chart-2', {
      labels: job_name_labels,
      series: [
        job_success,
        job_failed,
      ]
    }, {
      seriesBarDistance: 10,
      axisX: {
        offset: 60
      },
      axisY: {
        offset: 80,
        labelInterpolationFnc: function(value) {
          return value
        },
        scaleMinSpace: 15
      }
    });                   
  }

  componentDidMount() {
    this.callApi()
    this.setJobLogs()
    this.barChart()  
    this.barChartData() 
  }
 

  async setInitialState(){
    this.setState(prevState => ({ 
      filter: '',
      limit: ''
    }), () => this.setJobLogs())
  }
 
  setJobLogs(){
    this.callApi().then(({jobLogs}) => {
      this.setState(prevState => ({
        jobLogs: jobLogs
      }))
    }).catch(error => {
      this.setState(prevState => ({
        error: error,
        loader: false
      }))
    });  
  }

 
  render(){

    var filter_button_name = ''
    var limit_button_name = ''
 
    if (this.state.filter !== '') {
      filter_button_name = this.state.filter
    }else {
      filter_button_name = "Add Filter (In Days)"
    }
 
    if (this.state.limit !== '') {
      limit_button_name = this.state.limit
    }else {
      limit_button_name = "Add limit"
    }

    if (this.state.error !== ''){
      return (
        <div class="container">
         <ul class = "pagination justify-content-center">
             <button class= "btn btn btn-sm" type="button " onClick = {() => {
                    this.setJobLogs() 
                }}>
               Refresh
             </button>
         </ul>
          <ErrorComponent error={this.state.error}/>
        </div>
      );
     } else {
    return (
      <div class="card">
      <div class="card-header">
      <a className = "btn btn-secondary btn-sm" data-toggle = "collapse"  href = "#streamnotify1" role = "button" 
        aria-expanded = "true" aria-controls = "streamnotify1">Etl Jobs Vs Count (Success/Failed)
      </a> 
        <ul class = "pagination float-right">
        <div class="dropdown">
            <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown">
              {filter_button_name}
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              { this.filterOperation.map ( filter => (
                <a class="dropdown-item">
                   <button class= "btn btn btn-sm" type="button " onClick = {() => {
                   this.setState(prevState => ({
                       filter: filter
                 }),() => this.setJobLogs())}}> {filter} </button>
                </a>
              ))}
            </div>
          </div>
          <div class="dropdown">
            <button type="button" class="btn btn-light dropdown-toggle" data-toggle="dropdown">
            {limit_button_name}
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              { this.limitOperation.map ( limit => (
                <a class="dropdown-item">
                   <button class= "btn btn btn-sm" type="button " onClick = {() => {
                   this.setState(prevState => ({
                       limit: limit
                 }),() => this.setJobLogs())}}> {limit} </button>
                </a>
              ))}
            </div>
          </div>
          <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
                this.setInitialState()
          }}>Clear</button>
          <button class= "btn btn btn-sm ml-2" type="button " onClick = {() => {
           
               this.setJobLogs() 
          
               var job_name_labels = []
               var job_success = []
               var job_failed = []

               this.state.jobLogs.map( (data) => {
                job_name_labels.push(data.job_name)
                job_success.push(data.success)
                job_failed.push(data.failed)
               })

              new Chartist.Bar('.ct-chart-2', {
                labels: job_name_labels,
                series: [
                  job_success,
                  job_failed,
                ]
              }, {
                seriesBarDistance: 10,
                axisX: {
                  offset: 60
                },
                axisY: {
                  offset: 80,
                  labelInterpolationFnc: function(value) {
                    return value
                  },
                  scaleMinSpace: 15
                }
              });                   
          }} >
          Refresh
          </button>
        </ul>      
        </div>
        <div className = "collapse" id = "streamnotify1">
        <div class= "card-body">
            <div class="ct-chart-2"></div>
        </div>
        </div>
  </div>
  );}
  }       
}
 
export default BarGraph;
 

